.header {
    top: 0;
    left: 0;
    margin-bottom: 10px;
    width: 100%;    
    position: static;
    z-index: 1100;
    display: flex;
    justify-content: left;
    align-items: center;
    min-height: 25px;
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  }
  
  